@charset "utf-8";
@import url('https://webfontworld.github.io/pretendard/Pretendard.css');



*{ 
	margin: 0; padding: 0; 
	font: inherit; color: inherit;
}
*,
*::before,
*::after{ box-sizing: border-box; }

:root {
	cursor:default; line-height:1.5; overflow-wrap:break-word; 
	-webkit-tap-highlight-color:transparent;
	-webkit-text-size-adjust:100%; text-size-adjust:100%; 
	-moz-tab-size:4;tab-size:4
}

:focus{outline: none;}

html, body {
  font-family: 'Pretendard' !important;
	min-height: 100vh; height:100%;
	line-height: 1.4;
}

a { 
	text-decoration:none; 
	display: flex;
	width: inherit; height: inherit;
}

ul, ul[role='list'],
ol, ol[role='list'] { list-style: none; }

img, svg,
video, canvas, picture{
	display: block;
	max-width: fit-content;
}

button {
	cursor:pointer; overflow:visible;
	border:0; border-radius:0; box-shadow:none; 
	background:none; 
}
button:focus { outline:none; }

table {
	border-spacing:0; 
	border-collapse:collapse;
}


.flex{
	display: flex;
	gap: 10px;
}

/* =================== */

.header{
  width: 100%; height: auto;
  padding: 20px 15px;
  background-color: black;
}
.header .logo-area{color:white}

.list-area{}
.list-area .list-wrap{border-top: 1px solid #878787; border-bottom: 1px solid #878787;}
.list-area .list-wrap li{
	padding: 10px 15px;
	display: flex; align-items: center;
}
.list-area .list-wrap .item{width: 100%;}
.list-area .list-wrap li + li {border-top: 1px solid #878787;}
.list-area .text-wrap { gap: 5px;
	display: flex; 
	flex-direction: column;
}
.list-area .text-wrap .title{ gap: 10px;
	font-weight: 600;
	
}
.list-area .text-wrap .text{font-size: 13px;}
.list-area .text-wrap .btn.like{gap: 5px;}
.list-area .text-wrap :is(.title, .btn.like){
	display: flex;
	align-items: center;
}
.list-area .text-body { display: none;
	margin-top: 15px;
	padding: 10px 15px;
	background-color: bisque;
}
.list-area .text-body.active{display: block;}